.stroke-none { stroke: none; }
.stroke-inherit { stroke: inherit; }
.stroke-current { stroke: currentColor; }
.stroke-transparent { stroke: transparent; }
.stroke-black { stroke: var(--color-black, black); }
.stroke-white { stroke: var(--color-white, white); }
.stroke-default { stroke: var(--color-default); }
.stroke-primary { stroke: var(--color-primary); }
.stroke-secondary { stroke: var(--color-secondary); }
.stroke-success { stroke: var(--color-success); }
.stroke-danger { stroke: var(--color-danger); }
.stroke-warning { stroke: var(--color-warning); }
.stroke-info { stroke: var(--color-info); }
.stroke-foreground { stroke: var(--color-foreground); }
.stroke-background { stroke: var(--color-background); }
.stroke-surface-1 { stroke: var(--color-surface-1); }
.stroke-surface-2 { stroke: var(--color-surface-2); }
